/* Global styles */
body {
  margin: 0;
  font-family: 'Poppins', sans-serif;
  scroll-behavior: smooth;
  background-color: #f7f9fc;
  color: #333;
}

/* Navbar */
nav {
  position: fixed;
  width: 100%;
  background: #1f2937;
  padding: 15px 0;
  top: 0;
  z-index: 1000;
  box-shadow: 0 4px 10px rgba(0,0,0,0.2);
}

nav ul {
  display: flex;
  justify-content: center;
  list-style: none;
  margin: 0;
  padding: 0;
}

nav ul li {
  margin: 0 25px;
}

nav ul li a {
  color: #fff;
  text-decoration: none;
  font-weight: 600;
  transition: 0.3s;
}

nav ul li a:hover {
  color: #00bcd4;
  transform: scale(1.1);
}

/* Header */
header {
  min-height: 100vh;
  background: linear-gradient(135deg, #00bcd4, #1f2937);
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
}

.header-container {
  max-width: 800px;
}

.profile-pic {
  width: 180px;
  height: 180px;
  border-radius: 50%;
  object-fit: cover;
  margin-bottom: 25px;
  border: 5px solid white;
  box-shadow: 0 8px 20px rgba(0,0,0,0.3);
  transition: transform 0.3s ease;
}

.profile-pic:hover {
  transform: scale(1.05);
}

/* Sections */
section {
  padding: 100px 20px;
  text-align: center;
}

section h2 {
  font-size: 2.5rem;
  margin-bottom: 20px;
  position: relative;
  display: inline-block;
}

section h2::after {
  content: '';
  display: block;
  width: 50%;
  height: 4px;
  background: #00bcd4;
  margin: 8px auto 0;
  border-radius: 2px;
}

/* About Section */
#about {
  background: #f4f6fa;
  max-width: 900px;
  margin: auto;
  border-radius: 12px;
  padding: 50px 30px;
  box-shadow: 0 8px 20px rgba(0,0,0,0.1);
}

#about p {
  font-size: 1.1rem;
  line-height: 1.8;
}

/* Skills Section */
.skills-section {
  max-width: 800px;
  margin: auto;
}

.skill {
  text-align: left;
  margin-bottom: 25px;
}

.skill span {
  font-weight: 600;
}

.skill-bar {
  background: #e0e0e0;
  border-radius: 12px;
  overflow: hidden;
  height: 22px;
  margin-top: 6px;
}

.skill-progress {
  height: 100%;
  background: #00bcd4;
  border-radius: 12px;
  transition: width 1s ease-in-out;
}

/* Projects Section */
#projects .projects-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  padding: 0;
}

.project-card {
  background: white;
  width: 250px;
  padding: 20px;
  border-radius: 12px;
  box-shadow: 0 8px 20px rgba(0,0,0,0.1);
  transition: transform 0.3s, box-shadow 0.3s;
}

.project-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 12px 25px rgba(0,0,0,0.2);
}

.project-card h3 {
  margin-bottom: 10px;
}

/* Contact Section */
#contact a {
  color: #00bcd4;
  text-decoration: none;
  font-weight: 600;
  transition: 0.3s;
}

#contact a:hover {
  text-decoration: underline;
}

/* Footer */
footer {
  padding: 30px 20px;
  background: #1f2937;
  color: white;
  text-align: center;
}
